.refund-container {
    display: grid;
    grid-template-columns: 19% 50% auto;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    color: #545454;
}
.refund-container .refund-details {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.refund-list-container  {
    margin-left: 30px;
}
.refund-list-container .refund-list {    
    display: list-item;
}
